.AltanQuestMap{
  border: 1px solid black;
  width: 100%;
  height: 100%;
  background: rgb(213,199,222);
  background: -moz-linear-gradient(357deg, rgba(213,199,222,1) 0%, rgba(188,130,224,1) 34%, rgba(168,77,226,1) 71%, rgba(151,30,228,1) 100%);
  background: -webkit-linear-gradient(357deg, rgba(213,199,222,1) 0%, rgba(188,130,224,1) 34%, rgba(168,77,226,1) 71%, rgba(151,30,228,1) 100%);
  background: linear-gradient(357deg, rgba(213,199,222,1) 0%, rgba(188,130,224,1) 34%, rgba(168,77,226,1) 71%, rgba(151,30,228,1) 100%);
  position: relative;
  overflow: auto;
  overflow-x: hidden;
}
.altanQuestTitle{
  text-align: center;
  font-size: 30px;
  font-family: 'Chelsea Market', cursive;
  -webkit-text-stroke: 1px black;
  stroke: 1px black;
  background: rgb(207,217,230);
  background: -moz-linear-gradient(124deg, rgba(207,217,230,1) 0%, rgba(177,194,214,1) 13%, rgba(179,201,230,1) 45%, rgba(150,176,209,1) 71%, rgba(158,191,235,1) 100%);
  background: -webkit-linear-gradient(124deg, rgba(207,217,230,1) 0%, rgba(177,194,214,1) 13%, rgba(179,201,230,1) 45%, rgba(150,176,209,1) 71%, rgba(158,191,235,1) 100%);
  background: linear-gradient(124deg, rgba(207,217,230,1) 0%, rgba(177,194,214,1) 13%, rgba(179,201,230,1) 45%, rgba(150,176,209,1) 71%, rgba(158,191,235,1) 100%);
  padding-bottom: 5px;
  border-bottom: 1px solid black;
  position: sticky;
  top: 0;
  z-index: 5;
}

.questBackButtonPosition{
  width:calc(80% - 3px) !important;
  margin: 0px !important;
}

.altanQuestButton{
  color: rgb(26, 26, 26);
  background: linear-gradient(135deg, rgba(220,220,220,1) 0%, rgb(153, 177, 229) 50%, rgb(173, 183, 206) 100%);
  border-radius: 5px;
  text-shadow: 2px 2px 4px #536481;
  cursor: pointer;
  position: relative;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 20px;
}

.altanStatsButton:hover{
  text-shadow: 1px 1px 1px #657c9c;
}


.questBackButtonPosition::before{
  position: absolute;
  top: 50%;
  left: 5px;
  width: 100%;
  height: 0px;
  border-top: 7px solid transparent;  
  border-bottom: 7px solid transparent;
  border-left: 10px solid #764abc;
  content: '';
  transition: all 0.1s;
  transform: translate3d(0,-50%,0) scale3d(0,0,1);
  opacity: 0;
}

.questBackButtonPosition:hover::before{
  transform: translate3d(0,-50%,0) scale3d(1,1,1);
  opacity: 1;
}
