  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  position: fixed;  
  top:calc(75vh - 3rem - 29px);
}


.questBox{
  margin-top: 20px;
  padding-bottom: 35px;
}

.questList{
  margin-top:10px;
}

